.photo-container ul {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-align-items: flex-start;
  -ms-flex-align: start;
      align-items: flex-start;
}

.photo-container ul li {
  width: 220px;
  height: 165px;
  overflow: hidden;
  margin-bottom: 15px;
}

.photo-container ul img {
  width: 100%;
  -webkit-transform: scale(1.25);
      -ms-transform: scale(1.25);
          transform: scale(1.25);
  -webkit-transition: -webkit-transform 1.25s;
  transition: -webkit-transform 1.25s;
  -o-transition: transform 1.25s;
  transition: transform 1.25s;
  transition: transform 1.25s, -webkit-transform 1.25s;
}

.photo-container li:hover img {
  -webkit-transform: scale(1.65);
      -ms-transform: scale(1.65);
          transform: scale(1.65);
}

.photo-container .not-found {
  width: 100%;
}

@media only screen and (max-width: 767px)  {
  .photo-container li {
    margin: auto;
  }
}



